@import "./../../banner.scss";

.page-banner.page-banner_medium {
  height: 518px;
  .picture-block {
    source {
    }
  }
  .banner-container {
    margin-top: 20px;
    &__title {
      font-family: Raleway;
      font-size: 45px;
      font-weight: 800;
      font-stretch: normal;
      font-style: normal;
      line-height: 1;
      letter-spacing: 0.45px;
      text-align: center;
      color: #fff;
      @media (max-width: $mobile) {
        font-size: 35px;
      }
    }
    &__description {
      max-width: 420px;
      font-family: Raleway;
      font-size: 24px;
      font-weight: 500;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.25;
      letter-spacing: 0.24px;
      text-align: center;
      color: #fff;
      @media (max-width: $mobile) {
        font-size: 35px;
      }
    }
  }
}
