.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.body-banner {
  background: rgba(0, 0, 0, 0.45);
  position: relative;
  height: 600px;
  overflow: hidden;
}
.body-banner.video {
  cursor: pointer;
}
.body-banner.video:hover {
  background: rgba(0, 0, 0, 0.55);
}
@media (max-width: 960px) {
  .body-banner {
    max-height: 450px;
  }
}
@media (max-width: 600px) {
  .body-banner {
    max-height: 250px;
  }
}
.body-banner img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.body-banner .banner-container {
  margin-top: 195px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: 100%;
  gap: 28px;
}
@media (max-width: 960px) {
  .body-banner .banner-container {
    margin-top: auto;
    justify-content: center;
  }
}
@media (max-width: 600px) {
  .body-banner .banner-container {
    gap: 11px;
  }
}
.body-banner .banner-container__title {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-size: 62px;
  line-height: 73px;
  letter-spacing: 0.01em;
  color: #ffffff;
}
@media (max-width: 960px) {
  .body-banner .banner-container__title {
    font-size: 40px;
    line-height: 45px;
  }
}
@media (max-width: 600px) {
  .body-banner .banner-container__title {
    font-size: 28px;
    line-height: 33px;
  }
}
@media (max-width: 600px) {
  .body-banner .banner-container__title {
    margin-top: 17px;
    margin-top: 0;
  }
}
.body-banner .banner-container__description {
  max-width: 550px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  letter-spacing: 0.01em;
  line-height: 32px;
  margin-top: 2px;
  margin-right: 1px;
  color: #ffffff;
  text-align: center;
}
@media (max-width: 600px) {
  .body-banner .banner-container__description {
    font-size: 17px;
    line-height: 20px;
  }
}
@media (max-width: 600px) {
  .body-banner .banner-container__description {
    margin-top: 0;
    max-width: 300px;
    line-height: 23px;
    letter-spacing: 0.01em;
  }
}
.body-banner .banner-container .icon-play {
  margin-top: 10px;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 61px;
  height: 61px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  font-size: 17px;
}
.body-banner .banner-container .icon-play::before {
  margin-left: 5px;
}
@media (max-width: 600px) {
  .body-banner .banner-container .icon-play {
    width: 45px;
    height: 45px;
  }
  .body-banner .banner-container .icon-play::before {
    font-size: 13px;
  }
}
